<template>
    <div class="construction-con">
        <div class="text-container">
            <h1 class="title">LIFE IS MESSY</h1>
            <div class="subscribe-box">
                <h4 class="message">{{ message }}</h4>
                <h6>enter your email to subscribe for updates</h6>
                <input v-model="email" />
                <button class="button" @click="submit">subscribe</button>
            </div>
            <h1 class="animate-me">
                <span class="letter">u</span>
                <span class="letter">n</span>
                <span class="letter">d</span>
                <span class="letter">e</span>
                <span class="letter">r</span>
                <span class="letter">__</span>
                <span class="letter">c</span>
                <span class="letter">o</span>
                <span class="letter">n</span>
                <span class="letter">s</span>
                <span class="letter">t</span>
                <span class="letter">r</span>
                <span class="letter">u</span>
                <span class="letter">c</span>
                <span class="letter">t</span>
                <span class="letter">i</span>
                <span class="letter">o</span>
                <span class="letter">n</span>
            </h1>
        </div>
    </div>

</template>

<script>
    import anime from 'animejs'
    import { NODE } from '../axios'
    export default {
        name: 'underconstruction',
        data () {
            return {
                email: '',
                message: ''
            }
        },
        mounted () {
            anime.timeline()
                .add({
                    targets: '.animate-me .letter',
                    translateY: [-120, 10],
                    easing: "easeOutExpo",
                    duration: 2400,
                    delay: function(el, i) {
                        return 180 * i;
                    }
                })
        },
        methods: {
            submit() {
                if (this.email === '') {
                    this.message = 'Bro, you need to enter an email'
                } else {
                    NODE.post('/api/subscribe', this.email)
                        .then(() => {
                            this.message = "We'll be in touch soon!"
                        })
                        .catch(() => {
                            this.message = "Something went wrong, try again later"
                        })
                }
            }
        },
        computed: {
        }
    }
</script>
